import * as React from 'react';
import {
  Button,
  CompassContent,
  CompassHero,
  CompassPanel,
  Content,
  Flex,
  FlexItem,
  Label,
  MenuToggle,
  MenuToggleElement,
  SearchInput,
  Select,
  SelectList,
  SelectOption,
} from '@patternfly/react-core';
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
  TimesCircleIcon,
  ArrowRightIcon,
} from '@patternfly/react-icons';
import { Table, Thead, Tr, Th, Tbody, Td } from '@patternfly/react-table';
import hummingbirdImg from '../bgimages/image-logo.png';

// Security Feed data matching the screenshot
interface SecurityFeedRow {
  name: string;
  project: string;
  status: 'healthy' | 'warning' | 'crashed';
}

const securityFeedData: SecurityFeedRow[] = [
  { name: 'my-pod-name', project: 'project-test', status: 'healthy' },
  { name: 'my-pod-name', project: 'project-test', status: 'healthy' },
  { name: 'my-pod-name', project: 'project-test', status: 'healthy' },
  { name: 'my-pod-name', project: 'project-test', status: 'warning' },
  { name: 'my-pod-name', project: 'project-test', status: 'crashed' },
  { name: 'my-pod-name', project: 'project-test', status: 'healthy' },
  { name: 'my-pod-name', project: 'project-test', status: 'crashed' },
  { name: 'my-pod-name', project: 'project-test', status: 'crashed' },
];

const StatusLabel: React.FC<{ status: SecurityFeedRow['status'] }> = ({ status }) => {
  switch (status) {
    case 'healthy':
      return <Label color="green" icon={<CheckCircleIcon />} isCompact>Healthy</Label>;
    case 'warning':
      return <Label color="yellow" icon={<ExclamationTriangleIcon />} isCompact>Warning</Label>;
    case 'crashed':
      return <Label color="red" icon={<TimesCircleIcon />} isCompact>Crashed</Label>;
  }
};

const Dashboard: React.FunctionComponent = () => {
  const [searchValue, setSearchValue] = React.useState('');
  const [isFilterOpen, setIsFilterOpen] = React.useState(false);
  const [selectedFilter, setSelectedFilter] = React.useState<string>('All');
  
  const filterOptions = ['All', 'Healthy', 'Warning', 'Crashed'];

  const filteredData = securityFeedData.filter(row => {
    const matchesSearch = row.name.toLowerCase().includes(searchValue.toLowerCase()) ||
                          row.project.toLowerCase().includes(searchValue.toLowerCase());
    const matchesFilter = selectedFilter === 'All' || 
                          row.status.toLowerCase() === selectedFilter.toLowerCase();
    return matchesSearch && matchesFilter;
  });

  return (
    <CompassContent>
      {/* Hero Banner - matching the screenshot design */}
      <CompassHero 
        style={{
          marginBottom: 'var(--pf-t--global--spacer--lg)',
          '--pf-v6-c-hero--BackgroundColor': 'var(--pf-t--global--background--color--secondary--default)',
        } as React.CSSProperties}
      >
        <Flex 
          alignItems={{ default: 'alignItemsCenter' }} 
          justifyContent={{ default: 'justifyContentSpaceBetween' }}
          flexWrap={{ default: 'wrap' }}
          gap={{ default: 'gapLg' }}
        >
          <FlexItem flex={{ default: 'flex_1' }}>
            <div style={{ maxWidth: '480px' }}>
              <Label 
                color="blue" 
                isCompact 
                style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}
              >
                STICKY
              </Label>
              <Content component="h1" style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
                Project Hummingbird
              </Content>
              <Content component="p" style={{ 
                color: 'var(--pf-t--global--text--color--subtle)',
                marginBottom: 'var(--pf-t--global--spacer--lg)'
              }}>
                Red Hat is excited to introduce <strong>Hummingbird container images</strong>.
                These container images are near-zero CVE, lightweight images ready 
                to plug and play with your existing stack.
              </Content>
              <Flex gap={{ default: 'gapMd' }}>
                <FlexItem>
                  <Button variant="primary">Request an image</Button>
                </FlexItem>
                <FlexItem>
                  <Button variant="link" icon={<ArrowRightIcon />} iconPosition="end">
                    Learn more
                  </Button>
                </FlexItem>
              </Flex>
            </div>
          </FlexItem>
          <FlexItem>
            <img 
              src={hummingbirdImg} 
              alt="Hummingbird" 
              style={{ 
                maxWidth: '280px', 
                height: 'auto',
                objectFit: 'contain'
              }} 
            />
          </FlexItem>
        </Flex>
      </CompassHero>

      {/* Security Feed Section */}
      <CompassPanel className="pf-m-no-padding">
        <div style={{ padding: 'var(--pf-t--global--spacer--lg)' }}>
          {/* Header with title and controls */}
          <Flex 
            justifyContent={{ default: 'justifyContentSpaceBetween' }} 
            alignItems={{ default: 'alignItemsCenter' }}
            flexWrap={{ default: 'wrap' }}
            gap={{ default: 'gapMd' }}
            style={{ marginBottom: 'var(--pf-t--global--spacer--lg)' }}
          >
            <FlexItem>
              <Content component="h2" style={{ margin: 0 }}>Security Feed</Content>
            </FlexItem>
            <FlexItem>
              <Flex gap={{ default: 'gapMd' }} alignItems={{ default: 'alignItemsCenter' }}>
                <FlexItem>
                  <SearchInput
                    placeholder="Search"
                    value={searchValue}
                    onChange={(_event, value) => setSearchValue(value)}
                    onClear={() => setSearchValue('')}
                    style={{ width: '200px' }}
                  />
                </FlexItem>
                <FlexItem>
                  <Select
                    id="filter-select"
                    isOpen={isFilterOpen}
                    selected={selectedFilter}
                    onSelect={(_event, value) => {
                      setSelectedFilter(value as string);
                      setIsFilterOpen(false);
                    }}
                    onOpenChange={setIsFilterOpen}
                    toggle={(toggleRef: React.Ref<MenuToggleElement>) => (
                      <MenuToggle
                        ref={toggleRef}
                        onClick={() => setIsFilterOpen(!isFilterOpen)}
                        isExpanded={isFilterOpen}
                        style={{ minWidth: '100px' }}
                      >
                        {selectedFilter}
                      </MenuToggle>
                    )}
                  >
                    <SelectList>
                      {filterOptions.map((option) => (
                        <SelectOption key={option} value={option}>
                          {option}
                        </SelectOption>
                      ))}
                    </SelectList>
                  </Select>
                </FlexItem>
              </Flex>
            </FlexItem>
          </Flex>

          {/* Recent Activity Table */}
          <div style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
            <Flex alignItems={{ default: 'alignItemsCenter' }} gap={{ default: 'gapSm' }}>
              <FlexItem>
                <span style={{ 
                  fontSize: 'var(--pf-t--global--font--size--body--default)',
                  color: 'var(--pf-t--global--text--color--subtle)'
                }}>
                  ★
                </span>
              </FlexItem>
              <FlexItem>
                <strong>Recent activity</strong>
              </FlexItem>
            </Flex>
          </div>

          <Table variant="compact" borders={false}>
            <Thead>
              <Tr>
                <Th width={40}>Name</Th>
                <Th width={30}>Project</Th>
                <Th width={20}>Status</Th>
                <Th width={10}></Th>
              </Tr>
            </Thead>
            <Tbody>
              {filteredData.map((row, index) => (
                <Tr key={index}>
                  <Td dataLabel="Name">
                    <a href="#" style={{ color: 'var(--pf-t--global--text--color--link--default)' }}>
                      {row.name}
                    </a>
                  </Td>
                  <Td dataLabel="Project">
                    <a href="#" style={{ color: 'var(--pf-t--global--text--color--link--default)' }}>
                      {row.project}
                    </a>
                  </Td>
                  <Td dataLabel="Status">
                    <StatusLabel status={row.status} />
                  </Td>
                  <Td dataLabel="Actions" isActionCell>
                    <Button variant="plain" aria-label="Kebab menu">
                      ⋮
                    </Button>
                  </Td>
                </Tr>
              ))}
            </Tbody>
          </Table>
        </div>
      </CompassPanel>
    </CompassContent>
  );
};

export { Dashboard };
